import React, { useState, useEffect } from 'react';
import { IconButton, Snackbar, SnackbarContent } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import CloseIcon from '@material-ui/icons/Close';

import clsx from 'clsx';

const makeSnackbarStyles = makeStyles(() => ({
  container: {
    top: 'auto',
    bottom: '25px',
    border: '1px solid #707070',
    borderRadius: '3px',
    backgroundColor: 'rgba(54, 54, 54, 0.75)',
    zIndex: 999,
  },
  message: {
    color: '#FFFFFF',
    display: 'flex',
    alignItems: 'center',
  },
  link: {
    marginLeft: '4px',
    color: '#FFFFFF',
    textDecoration: 'underline',
    cursor: 'pointer',
  },
  icon: {
    fontSize: 20,
    color: '#FFFFFF',
  },
  iconContainer: {
    marginTop: '-48px',
    marginRight: '-12px',
  },
}));

interface MySnackbarContentWrapperProps {
  ticket: any;
  onViewTicket: (ticket: any) => void;
  onClose: () => void;
}

const MySnackbarContentWrapper: React.FC<MySnackbarContentWrapperProps> = (
  props: MySnackbarContentWrapperProps,
): JSX.Element => {
  const classes = makeSnackbarStyles();

  const onViewTicket = () => {
    if (props.onViewTicket) {
      props.onViewTicket(props.ticket);
    }
  };

  const onClose = () => {
    if (props.onClose) {
      props.onClose();
    }
  };

  return (
    <SnackbarContent
      aria-describedby="dashboard-SnackbarContent"
      message={
        <div className={classes.message}>
          Ticket ID {props.ticket.ticket_id} has been claimed.
          <span
            id="client-snackbar"
            className={classes.link}
            onClick={onViewTicket}
          >
            View Ticket
          </span>
        </div>
      }
      action={
        <IconButton
          key="close"
          aria-label="close"
          color="inherit"
          onClick={onClose}
          className={classes.iconContainer}
        >
          <CloseIcon className={classes.icon} />
        </IconButton>
      }
    />
  );
};

MySnackbarContentWrapper.defaultProps = {
  ticket: {},
  onClose: () => {},
};

interface TicketSnackbarNotificationProps {
  isOpen: boolean;
  ticket: any;
  onViewTicket: (ticket: any) => void;
  onSnackbarClose: () => void;
}

export const TicketSnackbarNotification: React.FC<
  TicketSnackbarNotificationProps
> = (props: TicketSnackbarNotificationProps): JSX.Element => {
  const classes = makeSnackbarStyles();

  const [isSnackbarOpen, setIsSnackbarOpen] = useState(props.isOpen);

  useEffect(() => {
    setIsSnackbarOpen(props.isOpen);
  }, [props.isOpen]);

  function onSnackbarClose() {
    setIsSnackbarOpen(false);

    if (props.onSnackbarClose) {
      props.onSnackbarClose();
    }
  }

  return (
    <Snackbar
      open={isSnackbarOpen}
      className={clsx(classes.container)}
      onClose={onSnackbarClose}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'right',
      }}
    >
      <MySnackbarContentWrapper
        ticket={props.ticket}
        onClose={onSnackbarClose}
        onViewTicket={props.onViewTicket}
      />
    </Snackbar>
  );
};

TicketSnackbarNotification.defaultProps = {
  isOpen: false,
  ticket: {},
};
